.investor-section {
  background-color: #f0f0f0; /* light grey background */
  padding: 3% 9%; /* top/bottom 3%, left/right 8% */
  display: flex;
  justify-content: flex-start; /* content aligned left */
  align-items: center;
  text-align: left;
  flex-wrap: wrap; /* allows wrapping if needed */
}

.investor-container {
  width: 90%; /* âœ… take full width */
  max-width: none; /* âœ… remove restriction */
  margin-left: 0; /* âœ… remove forced offset */
}

.investor-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: #F8AA15; /* title color */
}

.investor-text {
  font-size: 1.875rem; /* 30px */
  font-weight: bold;
  margin-bottom: 2rem;
  color: #172F55;
  line-height: 1.6;
  width: 100%; /* âœ… text takes full container width */
}

.investor-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Buttons base style */
.investor-btn {
  padding: 0.75rem 2rem;
  font-size: 1rem;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold; /* make text bold */
  color: #000; /* black text */
}

/* Left button */
.primary-btn {
  background-color: #F8AA15;
  color: #000; /* ensure text is black */
  font-weight: bold; /* bold text */
}

.primary-btn:hover {
  background-color: #d18a10;
  color: #000; /* keep text black on hover */
}

/* If you have other buttons, you can do the same */
.secondary-btn {
  background-color: #ffffff; /* example */
  color: #000;
  font-weight: bold;
  border: 2px solid #F8AA15;
}

.secondary-btn:hover {
  background-color: #f8f8f8;
}

/* Right button */




/* Responsive */
@media (max-width: 768px) {
  .investor-section {
    justify-content: center;
    text-align: center;
  }

  .investor-container {
    width: 100%;
    max-width: 100%; /* make sure it stays full width */
    margin-left: 0;
  }

  .investor-title {
    font-size: 2rem;
  }

  .investor-text {
    font-size: 1rem;
    width: 100%;
  }

  .investor-buttons {
    flex-direction: column;
    gap: 1rem;
  }
}
