/* Footer.css */

.footer {
  background-color: white;
  color: #0a2540;
  padding: 2rem 1rem;
  border-top: 2px solid #eee;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
  align-items: start;
}

.footer-logo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.2rem;
  margin-top: -30px;
  margin-left: 0; /* move fully to the left */
}

.footer-logo img {
  height: 80px; /* slightly smaller for balance */
  margin-bottom: 0.1rem;
}

.powered-by {
  font-size: 0.95rem;
  color: #000000;
  margin: 0;
  margin-left: 20px; /* shift subtitle slightly to the right */
}

/* Social icons horizontal, pushed down */
.social-icons {
  display: flex;
  flex-direction: row; /* horizontal */
  gap: 1rem; /* space between icons */
  margin-top: 2rem; /* move down below logo/description */
}

.social-icons a svg {
  width: 30px;
  height: 30px;
  fill: #f81515; /* red color */
  transition: transform 0.3s ease;
}

.social-icons a:hover svg {
  transform: scale(1.2);
}

.footer-links {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.footer-links .email-label {
  font-size: 0.95rem;
  margin: 0;
}

.footer-links .email {
  margin: 0 0 0.5rem 0;
}

.footer-links a {
  color: #0a2540;
  text-decoration: none;
  font-weight: 500;
}

.footer-links a:hover {
  text-decoration: underline;
}

.footer-newsletter {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-left: auto; /* pushes newsletter further right */
}

.newsletter-title {
  margin: 0 0 0.5rem 0;
  font-size: 2rem; /* increase title size */
  font-weight: bold;
}

.footer-newsletter form {
  display: flex;
}

.footer-newsletter input {
  flex: 1;
  padding: 0.5rem;
  border-radius: 5px 0 0 5px;
  border: 1px solid #ccc;
  outline: none;
}

.footer-newsletter button {
  background-color: #0a2540;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-weight: bold;
}

.footer-newsletter button:hover {
  background-color: #142f54;
}

/* Responsive: Stack on small screens */
@media (max-width: 768px) {
  .footer-container {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .footer-logo,
  .footer-links,
  .footer-newsletter {
    align-items: center;
  }

  .powered-by {
    margin-left: 0; /* reset indentation for mobile */
  }

  .social-icons {
    justify-content: center;
    gap: 1rem;
  }
}
