/* Hero Section */
.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #0a1d4c;
}

/* Hero Background Image */
.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  opacity: 0;
  transition: opacity 0.6s ease-in-out;
}

.hero-image.active {
  opacity: 1;
}

/* Shadow content box */
.hero-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 40%;
  height: 100%;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 8rem 2rem 3rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 1rem;
  color: #fff;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.5);
}

.hero-text h1 {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.3;
}

.hero-text p {
  font-size: 1.3rem;
  line-height: 1.6;
  color: #ffffff;
  -webkit-text-stroke: 1px #F8AA15;
  paint-order: stroke fill;
}

/* Buttons top right */
.hero-buttons-top {
  position: absolute;
  top: 20px;
  right: 30px;
  display: flex;
  gap: 10px;
  z-index: 10;
}

.hero-buttons-top .btn-primary,
.hero-buttons-top .btn-secondary {
  padding: 0.6rem 1.5rem;
  font-size: 0.9rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-primary {
  background-color: #f81515;
  color: #fff;
  border: none;
}

.btn-primary:hover {
  background-color: #d11010;
}

.btn-secondary {
  background-color: #000;
  color: #fff;
  border: 2px solid #f81515;
}

.btn-secondary:hover {
  background-color: #222;
}

/* === Modal Overlay === */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

/* === Modal Content === */
.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 12px;
  max-width: 500px;
  width: 90%;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
  animation: fadeIn 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-content h2 {
  font-size: 1.5rem;
  color: #0a1d4c;
  margin-bottom: 0.5rem;
}

.modal-content form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-content input,
.modal-content textarea {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
}

.modal-content textarea {
  min-height: 100px;
  resize: vertical;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 10px;
}

.modal-btn {
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Send Button */
.send-btn {
  background-color: #f81515;
  color: #fff;
  box-shadow: 0 4px 10px rgba(248, 21, 21, 0.3);
}

.send-btn:hover {
  background-color: #d11010;
  transform: scale(1.05);
}

/* Cancel Button */
.cancel-btn {
  background-color: #ddd;
  color: #333;
}

.cancel-btn:hover {
  background-color: #bbb;
  transform: scale(1.05);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Responsive */
@media (max-width: 1024px) {
  .hero-content {
    left: 3%;
    max-width: 70%;
    padding: 2rem;
    top: 38%;
  }
}

@media (max-width: 768px) {
  .hero-content {
    left: 50%;
    width: 80%;
    max-width: 500px;
    padding: 1.5rem 1rem;
    transform: translate(-50%, -50%);
    text-align: center;
    top: 42%;
  }

  .hero-text h1 {
    font-size: 1.8rem;
  }

  .hero-text p {
    font-size: 1rem;
  }

  .hero-buttons-top {
    left: 50px;
    right: auto;
    top: 15px;
    flex-direction: row;
    gap: 8px;
  }

  .hero-buttons-top .btn-primary,
  .hero-buttons-top .btn-secondary {
    font-size: 0.85rem;
    padding: 0.5rem 0.5rem;
    white-space: nowrap;
  }
}
